import BaseApiService from "../../BaseApiService";

const DEFAULT_PATH = "/custom/api/product";

class ProductConfigProductService extends BaseApiService {
  getAll = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?start=${start}&limit=${limit}${search}`;
    return this.makeGetRequestWithAuth(url);
  };
  findByName = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/search?name:lk=${search}&start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllLoanData = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/loan/interest/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllInstallmentData = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/installment/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllCustomerTypeData = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/customer/type/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllPropertyTypeData = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/property/type/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllTransactionTypeData = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/transaction/type/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getAllAttachmentData = (start = 0, limit = 10, search = "") => {
    const url = `${DEFAULT_PATH}/attachment/mapping/search?${search}start=${start}&limit=${limit}`;
    return this.makeGetRequestWithAuth(url);
  };
  getProductInfo = (uid: string) => {
    const url = `${DEFAULT_PATH}/manage/uid/${uid}`;
    return this.makeGetRequestWithAuth(url);
  };
  save = (obj: any) => {
    const url = `${DEFAULT_PATH}/manage`;
    return this.makePostRequestWithAuth(url, obj);
  };

  update = (obj: any, uid: string | undefined) => {
    const url = `${DEFAULT_PATH}/manage/${uid}`;
    return this.makePutRequestWithAuth(url, obj);
  };

  findById = (id: string) => {
    const url = `${DEFAULT_PATH}/uid/${id}`;
    return this.makeGetRequestWithAuth(url);
  };
  activate = (uid: string) => {
    const url = `${DEFAULT_PATH}/manage/activate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
  Inactivate = (uid: string) => {
    const url = `${DEFAULT_PATH}/manage/inactivate/${uid}`;
    return this.makePutRequestWithAuth(url);
  };
}

export default new ProductConfigProductService();
